.project-main{
  margin-top: 20vh;
  @include media-breakpoint-down(md){
    margin-top: 10vh;
  }
  &__img{
    img{
      width: 100%;
      height: auto;
    }
    @include media-breakpoint-down(md){
      margin-bottom: rem(30);
    }
  }
  a{
    font-weight: 600;
    color:$turquoise;
    text-decoration: underline;
  }
}